.adobeText {}

.logo span {
    font-weight: 700;
    font-size: 18px;
    color: #fa0f00;
}

.logo {
    gap: 0.5em;
    padding: 1em .5em;
    border-right: 1px solid #eaeaea;
}

.navbarItems {
    display: flex;
    flex: 1 1;
    align-items: baseline;
}

.navbarItems p {
    margin: 0;
    font-size: 12px;
    color: black;
    font-weight: 600;
}

.navSameItemsButton button {
    border: none;
    background: rgb(13, 102, 208);
    color: white;
    font-size:12px;
    padding: 0.7em 1.95em;
    border-radius: 33px;
}

.E-Signatures {
    border-right: 1px solid #eaeaea;

}

.navSameItems {
    padding: 1.2em 1em;
}

.signInNavbar button {
    background: no-repeat;
    border: none;
    color: #4B4B4B;
}

.signInNavbar button {
    cursor: pointer;
}

.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: 0 5px 5px rgb(139 142 142 / 29%);
    transform: translate3d(0, 0, 0);
}

/* IS User Sign In */
.isUserSignIn {
    width: 26px;
    height: 26px;
    background-image: url("../static/assest/signin/user.png");
    background-size: 26px;
    background-repeat: no-repeat;
    display: block;
}

.isUserSignIn:hover {
    cursor: pointer;
}

.signInWrapper {
    position: relative;
}

.userDragDropDown {
    width: 320px;
    position: absolute;
    right: 2px;
    top: 43px;
    z-index: 9;
    border: 1px solid #E1E1E1;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    background-color: #FFF;
    -webkit-font-smoothing: antialiased;
    box-shadow: 0 2px 6px -1px rgba(0, 0, 0, .1);
}

.createrName {
    color: black !important;
    text-decoration: underline;
}

.createrName:hover {
    cursor: pointer;
}

.userDragDropDown::before {
    content: '';
    width: 11px;
    border-color: #e1e1e1;
    height: 11px;
    border-width: 1px;
    background: white;
    border-style: solid;
    border-bottom: none;
    border-right: none;
    position: absolute;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    top: -6px;
    right: 10px;
    /* box-shadow: 0 2px 6px -1px rgba(0,0,0,.1); */
    z-index: 20000;
}

.ProfileMenu {
    padding: 6px 0;
    border-bottom: 1px solid #E1E1E1;
    margin: 0;
    ;
}

.ProfileTitle {
    padding: 8px 20px;
    color: #707070;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: 600;
}

.ProfileMenuLinks {
    list-style: none;
    margin: 0;
    padding: 0;
    text-decoration: none;
}

.ProfileMenuLinks li a {
    text-decoration: none;
    color: #4B4B4B;
    font-size: 14px;
    padding: 8px 20px;
    display: block;
}

.userInfo {
    display: flex;
    align-items: center;
    padding: 20px;
    gap: 1em;
    border-bottom: 1px solid #E1E1E1;
}

.ProfileData p {
    margin: 0;
}

.Profile-menu-text {}

.ProfileName {
    font-size: 18px;
    line-height: 1.4;
    font-weight: 700;
    color: #2C2C2C;
    padding: 0;
    margin: -3px 0 0;
    word-wrap: break-word;
}

.ProfileEmail {
    font-size: 14px;
    line-height: 1.25;
    color: #707070;
    word-break: break-all;
}

.ProfileMenuLinks {
    text-decoration: none;
    color: #4B4B4B;
    font-size: 14px;
    display: block;
}

.ProfileCta {
    font-size: 14px;
    padding-bottom: 1px;
    display: inline-block;
    color: #4B4B4B;
    box-shadow: inset 0 -1px 0 0 #4B4B4B;
}

.userAvatar {
    width: 64px;
    height: 64px;
    background-repeat: no-repeat;
    background: url("../static/assest/signin/user.png");
    background-size: 64px;

}
.navbarItemsMobile {
    padding: 0 0.9em;
}

.MobileNavbar {
    position: absolute;
    background: white;
    width: 100%;
    height: 80vh;
    z-index: 99;
}
.menuforMobile{
    display: none;
}
.menuforMobile{
    width: 60px;
    border: none;
    background: transparent;
    box-shadow: none;
    color: #2d2d2d;
    cursor: pointer;
    font-size: 20px;
    font-weight: 300;
}
.expand::before{
    content: "\2630";
}
.menuforMobileExpand::before{
    content: "\2715";
}
@media screen and (max-width:820px) {
    .navbarItems{
        display: none;
    }
    .menuforMobile{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .signInNavbar {
        flex: 1;
        justify-content: end;
    }
}
@media screen and (max-width:332px) {
   .userDragDropDown{
    width: 90vw;
   }
   .userAvatar{
    background-size: contain;
    background-repeat: no-repeat;
   }
   .ProfileName{
   font-size: 12px; 
   }

    .ProfileCta ,.ProfileTitle,.ProfileMenuLinks li a, .ProfileEmail{
        font-size: 9px;
    }
}