.App_Wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: #fc6767;
  padding-top: 2em;
  padding-bottom: .5em;
}
.centerD{
  display: flex;
  justify-content: center;
  align-items: center;
}
.signInNavbar{
  gap: .5em;
}
.pdfconverterWrapper {
  min-height: 84vh;
  width: 72%;
  background: white;
  padding: 2.9rem 1.9rem;
  border-radius: 12px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;

}
.downloadWrapper{
  height: 84vh;
  width: 72%;
  background: white;
  border-radius: 12px;
  box-sizing: border-box;
}
.imageWrapper h2{
  margin: 0;
}
.nextPageAlign{
flex-direction: row;
align-items: start;
}

.selectSectionWrapper {
  width: 100%;
  flex-direction: column;
  display: flex;
  justify-content: center;
  padding: 2.2rem 0;
  align-items: center;
  border: 3px dashed #e1e1e1;
  border-radius: 9px;
  position: relative;
  transition: all .2s ease-in-out ;
}

.selectSectionWrapper:hover {
  border-color: #b8b5b5;
  cursor: pointer;
}

.fileForUpload {
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
}
.imageWrapper {
  display: flex;
  justify-content: space-between;
  height: 100%;
  align-items: center;
  padding: 0 1rem;
}

.image-container {
  width: fit-content;
  height: fit-content;
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
}
.downloadButtonWrapper {
  flex: 1;
height: 100%;
border-right: 1px solid #00000047;
margin-right: 7px;
padding-right:1rem;
padding-top:1rem ;
display: flex;
flex-direction: column;
gap: 12px;
box-sizing: border-box;
}
.image-container img {
  width: fit-content;
  max-width: 100%;
}
.downloadButtonWrapper p{
  margin: 0;
  font-size: 12px;
  color: #4b4b4b;
}
button.downloadButton {
  background: transparent;
  border: 2px solid #fa0f00;
  border-radius: 22px;
  padding: 8px 29px;
}
button.downloadButton:hover {
  cursor: pointer;
}

.selectFile {
  background-color: #1473e6;
  border: none;
  color: white;
  padding: 10px 33px;
  border-radius: 30px;
  font-weight: 600;
  font-size: 14px;
}

.fileForUpload {
  cursor: pointer;
}

.logoInner {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 12px;
}

.pdfconverterWrapper span {
  font-weight: 700;
  font-size: 18px;
}

.pdfconverterWrapper h1 {
  color: #4b4b4b;
  font-size: 44px;
  margin: 0;
  margin-bottom: 1rem;
}

/* Sign In With Google Through FireBase */

.signInGoogle{
  background: transparent;
    border: 2px solid pink;
    border-radius: 22px;
    width: 100%;
    padding: 8px 29px;
}
.gsi-material-button {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -webkit-appearance: none;
  background-color: WHITE;
  background-image: none;
  border: 1px solid #747775;
  -webkit-border-radius: 20px;
  border-radius: 20px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #1f1f1f;
  cursor: pointer;
  font-family: 'Roboto', arial, sans-serif;
  font-size: 14px;
  height: 40px;
  letter-spacing: 0.25px;
  outline: none;
  overflow: hidden;
  padding: 0 12px;
  position: relative;
  text-align: center;
  -webkit-transition: background-color .218s, border-color .218s, box-shadow .218s;
  transition: background-color .218s, border-color .218s, box-shadow .218s;
  vertical-align: middle;
  white-space: nowrap;
  width: 100%;
}

.gsi-material-button .gsi-material-button-icon {
  height: 20px;
  margin-right: 12px;
  min-width: 20px;
  width: 20px;
}

.gsi-material-button .gsi-material-button-content-wrapper {
  -webkit-align-items: center;
  align-items: center;
  display: flex;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: nowrap;
  flex-wrap: nowrap;
  height: 100%;
  justify-content: center;
  position: relative;
  width: 100%;
}

.gsi-material-button .gsi-material-button-contents {
  -webkit-flex-grow: 0;
  flex-grow: 0;
  font-family: 'Roboto', arial, sans-serif;
  font-weight: 500;
  overflow: hidden;
  text-overflow: ellipsis;
  vertical-align: top;
}

.gsi-material-button .gsi-material-button-state {
  -webkit-transition: opacity .218s;
  transition: opacity .218s;
  bottom: 0;
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
}

.gsi-material-button:disabled {
  cursor: default;
  background-color: #ffffff61;
  border-color: #1f1f1f1f;
}

.gsi-material-button:disabled .gsi-material-button-contents {
  opacity: 38%;
}

.gsi-material-button:disabled .gsi-material-button-icon {
  opacity: 38%;
}

.gsi-material-button:not(:disabled):active .gsi-material-button-state, 
.gsi-material-button:not(:disabled):focus .gsi-material-button-state {
  background-color: #303030;
  opacity: 12%;
}

.gsi-material-button:not(:disabled):hover {
  -webkit-box-shadow: 0 1px 2px 0 rgba(60, 64, 67, .30), 0 1px 3px 1px rgba(60, 64, 67, .15);
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, .30), 0 1px 3px 1px rgba(60, 64, 67, .15);
}

.gsi-material-button:not(:disabled):hover .gsi-material-button-state {
  background-color: #303030;
  opacity: 8%;
}

@media screen and (max-width:800px) {
  .App_Wrapper{
 min-height: 100vh;
 padding: 1rem;
 justify-content: start;
  }
  .downloadWrapper{
    width: 100%!important;
  }
  .image-container{
    max-width: 80%;
  }
  .pdfconverterWrapper{
    width:100%!important;
  }
}


@media screen and (max-width:450px) {
  .pdfconverterWrapper{
    padding: 1.5rem 1.2rem;
  }
  .pdfconverterWrapper h1{
    font-size: 20px;
  }
  .imageWrapper{
    flex-direction: column;
    padding: 0 1em;
    gap: 22px;
  }
  .image-container img {
    border-bottom-right-radius: 12px;
    border-bottom-left-radius: 12px;
}
  .downloadButtonWrapper{
    border: none;
    height: initial; 
  margin-right: 0; 
     padding-right: 0;
  }
  .imgForUpload{
    width: 100%!important;
  }
  .downloadButtonWrapper ,.image-container{
    flex: 0;
  }
  .downloadWrapper{
    min-height: fit-content;
  }
  .image-container{
    max-width: 100%;
  }
  .selectSectionWrapper p{
    text-align: center;
  }
}